import Search from "../../components/Search/index.vue"
import {
	getPlayerList
} from "../../api/player.js"
import {
	getFilterItem
} from "../../utils/constant.js"
export default {
	data() {
		return {
			form: {
				playername: ""
			},
			playerList: []
		};
	},
	async onLoad() {
		const {
			code,
			data: {
				list
			}
		} = await getPlayerList()
		if (code > 0) {
			this.playerList = list.map(item => {
				item.roleIcon = getFilterItem('hero.role', item.role).icon;
				return item;
			});
		}
	},
	methods: {
		handleSearch(playerName) {
			console.log(playerName);
		},
		handleDetail({ id }) {
			uni.navigateTo({
				url: `./detail?id=${id}`
			});
		}
	},
	components: {
		Search
	}
}
